:root{--primary-background: #fff;--secondary-background: #fff;--font-color: #494C6B;--secondary-color: #3A7CFD;--border-color: #9da0bc;--header-image: url("./assets/bg-mobile-light.jpg")}.dark-mode{--primary-background: #171823;--secondary-background: #393A4B;--font-color: #fff;--border-color: #666;--header-image: url("./assets/bg-mobile-dark.jpg")}.pageContainer{background-color:var(--primary-background);font-family:"Josefin Sans",sans-serif;min-height:100vh}@media(min-width: 768px){:root{--header-image: url("./assets/bg-desktop-light.jpg")}.dark-mode{--header-image: url("./assets/bg-desktop-dark.jpg")}}.todo{margin:0;padding:0}.todo__container,.todo__actions{background-color:var(--secondary-background);border-radius:5px;box-shadow:0 7px 4px rgba(0,0,0,.2);margin-block-start:-2rem;margin-inline:1rem}.todo__item{align-items:center;border-bottom:var(--border-color) 1px solid;color:var(--font-color);display:flex;list-style:none;padding:.5rem}.todo__actions{display:flex;justify-content:space-around;margin-block-start:2rem;padding:1rem}.item__checkbox{appearance:none;aspect-ratio:1/1;border:1px solid var(--border-color);border-radius:50%;cursor:pointer;margin-inline-end:1rem;min-width:25px;position:relative;width:25px}.item__checkbox:checked{background-image:linear-gradient(135deg, #55DDFF, #C058F3);border:0}.item__checkbox:checked::before{border-radius:50%;color:#fff;content:"î¡¶";font-family:"Material Icons";font-size:15px;height:15px;left:50%;opacity:0;position:absolute;top:50%;transform:translate(-50%, -50%);transition:opacity .2s ease-in-out;width:15px}.item__checkbox:checked::before{opacity:1}.item__name{flex-grow:1}.item__close{background-color:rgba(0,0,0,0);border:0;color:var(--font-color);cursor:pointer;padding:1rem}.item__actions{color:var(--font-color);display:flex;justify-content:space-between;padding:1rem}.item__clear{background-color:rgba(0,0,0,0);border:0;color:var(--font-color);font-size:1rem}.action__btn{background-color:rgba(0,0,0,0);border:0;color:var(--font-color);cursor:pointer;font-size:1rem}.action__btn:hover{color:var(--secondary-color)}@media(min-width: 768px){.todo__container,.todo__actions{margin-inline:8rem}}@media(min-width: 1200px){.todo__container,.todo__actions{margin-inline:15rem}.todo__item{padding-inline:1rem}}